html {
    // theme
    --surface-a: #ffffff !important;
    --surface-a: #ffffff !important;
    --surface-b: #f8f9fa !important;
    --surface-c: #e9ecef !important;
    --surface-d: #dee2e6 !important;
    --surface-e: #ffffff !important;
    --surface-f: #ffffff !important;
    --text-color: #212529 !important;

    --bg-blue-dark-header: #efefef !important;
    --bg-blue-dark-border: #dee2e6 !important;
    --bg-blue-dark-content: #ffffff !important;
    --bg-blue-dark-color: #212529 !important;

    --bg-dark: #f8f9fa;
    --bg-dark-2: #f2f2f2;
    --bg-toggle: gray;
    --bg-toogle-rounded: skyblue;
    --bg-icon: #ccc;
    --color-light: #fff;
    --color-light-border-2: #bfbfbf;
    --color-active: #6c757d;
    --color-text: #3f3f3f;
    --color-text-select: #8b8b8b;
    --color-primary: #007bff;
    --color-info: #17a2b8;
    --color-muted: #6c757d;
    --color-success: #28a745;
    --color-danger: #dc3545;
    --color-dark-border: #515151;
    --primary: #8dd0ff;
    --bg-left-menu-hover: #7a7a7a38;
    --color-border-bottom: #3a3a3a;
    --bg-gray: #72777a;
    --color-light-border: #c3c3c3;
    --box-bottom-shadow: 0px 5px 23px 0px rgb(37, 37, 37);
    --shadow-right: 1px 1px 15px rgb(0 0 0 / 22%);
    --shadow-bottom: 1px 1px 15px rgb(0 0 0 / 44%);
    --box-shadow-inset: 0 0 10px #959595 inset;
    --box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.75);
    --box-shadow-focus: 0px 0px 5px 1px rgba(0, 0, 0, 0.35);
}

html[data-theme="dark"] {
    // theme
    --surface-a: #2a323d !important;
    --surface-b: #20262e !important;
    --surface-c: rgba(255, 255, 255, 0.04) !important;
    --surface-d: #3f4b5b !important;
    --surface-e: #2a323d !important;
    --surface-f: #2a323d !important;
    --text-color: rgba(255, 255, 255, 0.87) !important;

    --bg-blue-dark-header: #2d3745 !important;
    --bg-blue-dark-border: #3f4b5b !important;
    --bg-blue-dark-content: #2a323d !important;
    --bg-blue-dark-color: rgba(255, 255, 255, 0.87) !important;

    --bg-dark: #242424;
    --bg-dark-2: #2d2d2d;
    --bg-dark-select: #242424;
    --bg-toogle-rounded: #242424;
    --color-light: #fff;
    --color-light-border-2: #bfbfbf;
    --color-active: #6c757d;
    --color-text: #e5e5e5;
    --color-text-select: #8b8b8b;
    --color-primary: #007bff;
    --color-info: #17a2b8;
    --color-muted: #6c757d;
    --color-success: #28a745;
    --color-danger: #dc3545;
    --color-dark-border: #515151;
    --primary: #8dd0ff;
    --bg-left-menu-hover: #7a7a7a38;
    --color-border-bottom: #3a3a3a;
    --bg-gray: #72777a;
    --color-light-border: #474747;
    --box-bottom-shadow: 0px 5px 23px 0px rgb(37, 37, 37);
    --shadow-right: 1px 1px 15px rgb(0 0 0 / 44%);
    --shadow-bottom: 1px 1px 15px rgb(0 0 0 / 44%);
    --box-shadow-inset: 0 0 10px #959595 inset;
    --box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.521);
    --box-shadow-focus: 0px 0px 5px 1px rgba(184, 184, 184, 0.15);
}

html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
    transition: all 0.3s !important;
    transition-delay: 0 !important;
}

::-webkit-scrollbar {
    width: 7px;
}
::-webkit-scrollbar-track {
    background: var(--surface-a);
}
::-webkit-scrollbar-thumb {
    background: var(--surface-d);
}

.layout-config {
    position: fixed;
    padding: 0;
    top: 0;
    right: 0;
    display: block;
    width: 400px;
    z-index: 998;
    height: 100%;
    -webkit-transition: -webkit-transform 0.4s cubic-bezier(0.05, 0.74, 0.2, 0.99);
    transition: -webkit-transform 0.4s cubic-bezier(0.05, 0.74, 0.2, 0.99);
    transition: transform 0.4s cubic-bezier(0.05, 0.74, 0.2, 0.99);
    transition: transform 0.4s cubic-bezier(0.05, 0.74, 0.2, 0.99),
        -webkit-transform 0.4s cubic-bezier(0.05, 0.74, 0.2, 0.99);
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}
.layout-config .layout-config-content-wrapper {
    position: relative;
    height: 100%;
    -webkit-box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.24);
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.24);
    color: var(--text-color);
    background-color: var(--surface-f);
}
.layout-config .layout-config-content-wrapper .layout-config-button {
    display: block;
    position: absolute;
    width: 52px;
    height: 52px;
    line-height: 52px;
    background-color: var(--primary-color);
    text-align: center;
    color: var(--primary-color-text);
    top: 270px;
    left: -51px;
    z-index: -1;
    overflow: hidden;
    cursor: pointer;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    -webkit-box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14),
        0 3px 14px 2px rgba(0, 0, 0, 0.12);
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14),
        0 3px 14px 2px rgba(0, 0, 0, 0.12);
}
.layout-config .layout-config-content-wrapper .layout-config-button i {
    font-size: 26px;
    line-height: inherit;
    cursor: pointer;
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
    -webkit-transition: -webkit-transform 1s;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}
.layout-config.layout-config-active .layout-config-content-wrapper .layout-config-button i {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
.layout-config .layout-config-close {
    position: absolute;
    width: 25px;
    height: 25px;
    line-height: 25px;
    text-align: center;
    right: 20px;
    top: 20px;
    z-index: 999;
    background-color: var(--primary-color);
    border-radius: 50%;
    -webkit-transition: background-color 0.2s, -webkit-box-shadow 0.2s, -webkit-transform 0.2s;
    transition: background-color 0.2s, -webkit-box-shadow 0.2s, -webkit-transform 0.2s;
    transition: background-color 0.2s, box-shadow 0.2s, transform 0.2s;
    transition: background-color 0.2s, box-shadow 0.2s, transform 0.2s, -webkit-box-shadow 0.2s, -webkit-transform 0.2s;
}
.layout-config .layout-config-content {
    overflow: auto;
    height: 100%;
    padding: 2rem;
}
.p-grid {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -0.5rem;
    margin-left: -0.5rem;
    margin-top: -0.5rem;
}
.layout-config.layout-config-active {
    -webkit-transform: translateX(0);
    transform: translateX(0);
}

body {
    background-color: var(--surface-b) !important;
}

.layout {
    height: 100vh;
    width: 100%;
}

.navbar {
    position: fixed;
    right: 10px;
    width: calc(100% - 260px);
    height: 65px;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.4rem;
    transition-delay: 0 !important;
    border-bottom: 1px solid var(--surface-d);
    background-color: var(--surface-b);

    .navbar-left-item {
        display: flex;
        align-items: center;
        h2 {
            color: var(--text-color);
            opacity: 0.8;
        }
    }

    .navbar-right-item {
        display: flex;
        align-items: center;
    }
}

.menu-bars {
    margin-left: 1.5rem;
    font-size: 1.2rem;
    background: none;
    color: var(--text-color);
}

.nav-menu {
    display: flex;
    width: 240px;
    bottom: 0;
    overflow: hidden;
    top: 0;
    flex-direction: column;
    position: fixed;
    color: var(--text-color);
    z-index: 2;
    box-shadow: var(--shadow-right);
    background-color: var(--surface-a);
}

.nav-menu.close {
    width: 70px;
    .nav-text a {
        justify-content: center;
        padding: 0;
    }
    .nav-text a span {
        display: none;
    }
    .nav-text a svg {
        margin-right: 0;
    }
    .navbar-toggle span {
        display: none;
    }
    .navbar-toggle a svg {
        margin-right: 0;
    }
}

.navbar.close {
    width: calc(100% - 90px);
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 0px;
    list-style: none;
    height: 40px;
    a {
        text-decoration: none;
        color: var(--text-color);
        opacity: 0.8;
        font-size: 16px;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        padding: 0px 20px;
    }
    a svg {
        margin-right: 0.8rem;
        width: 25px;
        height: 20px;
    }
}

.nav-text:hover {
    border-right: 2px solid var(--color-info);
    a {
        background-color: var(--color-left-menu-hover);
    }
    span {
        color: var(--color-info);
    }
}

.nav-text.active {
    background-color: var(--color-left-menu-hover);
    border-right: 2px solid var(--color-info);
    a {
        color: var(--color-info);
        opacity: 1;
    }
}

.nav-menu-items {
    width: 100%;
    height: calc(100vh - 65px);
    padding-top: 2rem;
}

.navbar-toggle {
    width: 100%;
    height: 65px;
    display: flex;
    justify-content: center;
    // border-bottom: 1px solid var(--color-dark-border);
    a {
        color: var(--text-color);
        text-decoration: none;
        display: flex;
        align-self: center;
        align-items: center;
        font-size: 20px;
    }
    a svg {
        font-size: 35px;
        margin-right: 10px;
        color: skyblue;
    }
}

.page-container {
    width: calc(100% - 240px);
    left: 240px;
    top: 65px;
    height: auto;
    min-height: 90% !important;
    overflow-y: auto;
    float: left;
    position: relative;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.container-width {
    width: calc(100% - 70px);
    left: 70px;
}

.main-content {
    display: block;
    padding: 1rem;
    min-height: calc(100vh - 65px);
    width: auto;
}

.content-header {
    padding: 0 0.5rem;
    display: block;
    .breadcrumb {
        background: 0 0;
        line-height: 1.8rem;
        margin-bottom: 0;
        padding: 0;
    }
}

.container-fuild {
    width: 100%;
    padding-right: 7.5px;
    padding-left: 7.5px;
    padding-bottom: 7.5px;
    margin-right: auto;
    margin-left: auto;
}

.row {
    display: flex;
    justify-content: start !important;
    .header-item {
        color: var(--text-color);
    }
}

.breadcrumb {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
}

.breadcrumb-item.active {
    color: var(--color-active);
}

.breadcrumb-item + .breadcrumb-item::before {
    display: inline-block;
    padding-right: 0.5rem;
    color: var(--color-active);
    content: "/";
}

.breadcrumb-item + .breadcrumb-item {
    padding-left: 0.5rem;
}

.toggle-button {
    display: flex;
    align-items: center;
    color: var(--text-color);
    margin-bottom: 0.8rem;

    span:first-child {
        padding-right: 0.5rem;
    }
    span:last-child {
        padding-left: 0.5rem;
    }
}

.switch {
    position: relative;
    display: inline-block;
    min-width: 34px;
    width: 34px;
    height: 18px;

    input {
        opacity: 0;
        width: 0;
        height: 0;
    }

    .toggle-slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        box-shadow: var(--box-shadow-inset);
        background: var(--bg-toggle);
    }

    .toggle-slider::before {
        position: absolute;
        content: "";
        width: 12px;
        height: 12px;
        top: 3px;
        left: 3px;
        background-color: var(--bg-toogle-rounded);
    }

    input:checked + .toggle-slider::before {
        transform: translateX(16px);
    }

    .toggle-slider.rounded {
        border-radius: 34px;
    }

    .toggle-slider.rounded::before {
        border-radius: 34px;
    }
}

//Input
.input-container {
    display: flex;
    flex-direction: column;
    width: 100%;

    .input-header {
        color: var(--text-color);
        opacity: 0.8;
    }

    .input-group {
        display: flex;
        justify-content: space-between;
        margin-top: 0.2rem;
        border-radius: 5px;
        background-color: var(--bg-dark-2);
        // box-shadow: var(--box-shadow);
        border: 0.06rem solid var(--color-light-border);

        .input-icon {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 2.2rem;
        }

        .input-icon::before {
            content: "";
            position: relative;
            border-right: 1px solid var(--color-light-border);
            border-radius: 10px;
            left: 25px;
            width: 1px;
            height: 1.8rem;
            background-color: var(--bg-dark-2) !important;
        }

        .input {
            border: none;
            padding: 0 0.6rem;
            margin: 0;
            background: none;
            width: 100%;
            height: 2.3rem;
            color: var(--color-text);
            font-size: 0.95rem;
        }
    }

    .input-message {
        color: var(--color-danger);
        padding: 0.3rem 0;
        font-size: 0.95rem;
    }
}

.input-container + .button {
    margin-top: 1rem;
}

.select-container + .button {
    margin-top: 1rem;
}

//Select
.select-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    .select-header {
        color: var(--color-text);
        opacity: 0.8;
    }
    .select-group {
        display: flex;
        justify-content: space-between;
        margin-top: 0.2rem;
        border-radius: 5px;
        background-color: var(--bg-dark-2);
        // box-shadow: var(--box-shadow);
        border: 0.06rem solid var(--color-light-border);
        .css-2b097c-container {
            width: 100%;
        }
        .css-1rhbuit-multiValue {
            background: var(--bg-dark);
            color: var(--color-text-select);
        }
        .css-12jo7m5 {
            color: var(--color-text-select);
        }
        .css-1uccc91-singleValue {
            color: var(--color-text);
        }
        .select-icon {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 2.2rem;
        }
        .select-icon::before {
            content: "";
            position: relative;
            border-right: 1px solid var(--color-light-border);
            border-radius: 10px;
            left: 25px;
            width: 1px;
            height: 1.8rem;
            background-color: var(--bg-dark-2) !important;
        }
    }
    .select-message {
        color: var(--color-danger);
        padding: 0.3rem 0;
        font-size: 0.95rem;
    }
}

//Button
.button {
    display: inline-block;
    border: none;
    padding: 0.6rem 1.5rem;
    color: var(--color-text);
    border-radius: 0.3rem;
    cursor: pointer;
    font-size: 0.9rem;
    box-shadow: var(--box-shadow);
    border: 0.06rem solid var(--color-light-border);
    span {
        opacity: 0.8;
        color: var(--color-text);
    }
}

.button-default {
    background: var(--bg-dark-2);
}

.button-primary {
    background-color: var(--color-primary);
}

.button-block {
    display: block;
    width: 100%;
}

button:active {
    opacity: 0.8;
}

