.table-responsive {
    width: auto;
    overflow-x: auto;
}

.table {
    width: 99.99%;
    border-collapse: collapse;

    thead tr th {
        height: 2.7rem;
        text-align: left;
        padding: 0.5rem;
        color: var(--color-text);
    }

    tbody {
        tr:not(.loader) {
            border: 1px solid var(--surface-d);
            transition: background-color 0.15s;

            &:hover {
                cursor: pointer;
                background-color: var(--surface-b);
            }

            td:not(.loader) {
                height: 3rem;
                text-align: left;
                padding: 0.5rem;
                color: var(--color-text);

                .setting {
                    margin-right: 0.5rem;
                    cursor: pointer;
                }
            }
        }
    }
}

.properties {
    display: flex;
    justify-content: space-between;

    .right {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .p-field {
            margin: 0 0.7rem 0 0;
        }

        button {
            margin-right: 0.7rem;
            cursor: pointer;
        }
    }
}

.pagination {
    margin-top: 1rem;
    ul {
        list-style-type: none;
        display: flex;
        align-self: left;
        flex-wrap: nowrap;
    }
}

.pagination ul li {
    height: 2rem;
    width: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 3px 13px rgba(75, 75, 75, 0.29);
    box-shadow: var(--shadow);
    color: var(--color-text);
    font-size: .9rem;
    font-weight: 500;
    cursor: pointer;
    margin-right: .2rem;
    border-radius: .1rem;

